import {
	apiGetNavList
} from "../../common/js/video.js"
import {
	apiGetRelatedVideo
} from "../../common/js/index.js"
import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue"
export default {
	components: {
		MescrollUni
	},
	data() {
		return {
			navList: [{
				id: 32154,
				name: "推荐"
			}, {
				id: 2,
				name: "LOCK直播"
			}, {
				id: 3,
				name: "翻唱"
			}, {
				id: 4,
				name: "广播"
			}, {
				id: 5,
				name: "舞蹈"
			}, {
				id: 6,
				name: "游戏"
			}, {
				id: 7,
				name: "现场"
			}, ],
			curNav: 32154,
			sliderWidth: 60, // 导航默认宽度
			sliderOffset: 32, // 偏移量
			downOption: { //下拉刷新
				auto: true
			},
			upOption: { //上拉加载下一页的数据
				auto: true,
				page: {
					num: 0,
					size: 10
				}
			},
			relatedVideo: [] // 视频列表数据
		}
	},
	onLoad() {
		console.log("load")
		// this.getNavList()
		this.switchNav(this.curNav, 0);
	},
	onShow() {

	},
	onHide() {

	},
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 1000)
	},
	methods: {
		// 接口有问题，自定义了数组
		// getNavList() {
		// 	apiGetNavList({
		// 		id: 124
		// 	}).then(data => {
		// 		console.log(data)
		// 	})
		// },
		// 导航栏切换
		switchNav(e, index) {
			this.curNav = e
			//  导航栏的宽度 140 是第一项的宽度”LOCK直播“
			this.sliderWidth = index === 1 ? 140 : 60
			// 计算偏移量  32左边 + 文字60 + 间距32=126   
			this.sliderOffset = 124 * index + (index > 1 ? 80 : 20)
			this.getList(1, 5, data => {
				this.relatedVideo = data
			})
		},
		getList(pageNum, pageSize, successCallback, errCallback) {
			const params = {
				id: this.curNav,
				pageNum,
				pageSize
			}
			apiGetRelatedVideo(params).then(data => {
				successCallback && successCallback(data.data)
			})
		},
		downCallback(mescroll) {
			// console.log('downCallback')
			//重置列表为第一页(自动执行page.num=1，再触发upcallback方法)
			mescroll.resetUpScroll();
		},
		upCallback(mescroll) {
			// console.log('upCallback')
			this.getList(mescroll.num, mescroll.size, res => {
				//若是第一页 则清空
				if (mescroll.num == 1) {
					this.relatedVideo = [];
				}
				//累加数据
				this.relatedVideo = this.relatedVideo.concat(res);
				//成功的回调，隐藏刷新状态
				mescroll.endSuccess();
			}, () => {
				//失败的回调，隐藏下拉刷新状态
				mescroll.endErr();
			})
		}
	}

}
